/* Wave image background */
header{
    background-image: url(/website/static/media/bgImg.e3004027.png);
    height: 80vh;
    background-color: #355c7d;
    background-size: cover;
    background-repeat: 'no-repeat';
    background-position: bottom; /*This changes the wave flow in the image */
}

