nav{
    display: flex;
    justify-content: space-between; 
}

nav ul{
    list-style: none; /* dont display bullet points */
}

nav ul li{
    display: inline-block; /* do not display a line under anchor links text*/
    margin-right: 3vh;
  
}
nav ul li a{
    text-decoration: none;
    color: lightseagreen;
    font-size: 2vh;
    font-family: 'Turret Road', cursive;
}

